import React, {useEffect, useState} from 'react';
import './App.css';
import Recipe from './Recipe.js';

const App = () => {

  const APP_ID = "3db1f156";
  const APP_KEY = "c23f4541a668e4dd9900c92b9cac99c5";

  const [recipes, setRecipes] = useState([]);
  const [search, setSearch] = useState('');
  //Takes care of the value of the search once the form is submitted
  //so that it wont update every time 
  const [query, setQuery] = useState('chicken');

  //Allows the fetching of data
  useEffect( () => {
    getRecipes();
  }, [query]);

  //Does the fetching of the API chicken recipes
  //Gets triggered when the form is submitted
  //Stores the data fetched in 'recipes' using the setRecipes function
  const getRecipes = async () => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`);
    const data = await response.json();
    setRecipes(data.hits);
  }

  //When the form gets submitted, final 'search' value gets assigned to the 'query'
  //Thus, getRecipes would only fetch once
  const getSearch = e => {
    e.preventDefault();
    setQuery(search);
    setSearch('');
  }

  return (
    <div className="App">
      <form className="search-form" onSubmit={getSearch}>
        <input 
          autoFocus
          placeholder="Type in a keyword for a recipe..."
          className="search-bar" 
          type="text" 
          value={search}
          onChange={e => setSearch(e.target.value)}
        />
        <button 
          className="search-button"
          type="submit"
        >
          search
        </button>
      </form>

      <div className="recipes">
        {recipes.map(recipe => (
            <Recipe 
              key={recipe.recipe.label}
              name={recipe.recipe.label} 
              calories={recipe.recipe.calories} 
              url={recipe.recipe.image}
              ingredients={recipe.recipe.ingredients}
            />
          )
        )}
      </div>
      
    </div>
  )
}

export default App;